.sidebar {
    display: flex;
    position: absolute;
    left: 0px;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 50px 0px;
    width: 224px;
    height: 100%;
    background: linear-gradient(180deg, #000000 0%, rgba(15, 13, 15, 0.65) 100%);
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.25);
    z-index: 1;
}

.sidebar strong {
    width: 130px;
    text-align: center;
    font-family: Roboto, sans-serif;
    color: #FFF;
}

.sidebar button {
    all: unset;
    box-sizing: border-box;
    color: #FFF;
    padding: 18px 60px;
    cursor: pointer;
    background: #F0C551;
    border-radius: 6px;
    border: 1px solid #f0c551;

    font-family: Roboto;
    font-size: 14px;
    font-weight: 700;
    line-height: 16px;
    letter-spacing: 0em;
    text-align: left;

}

.puzzle {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.puzzle img {
    margin-bottom: 10px;
}